import {changeContent} from '../utils/change-content';
import {setBenefit, getLocation} from '../utils/location';

// модуль позволяет менять контент в benefit.html при клике на радио-кнопки
const changeBenefit = () => {
  const btn = document.querySelectorAll('.js-button-tabs'); // радио-кнопки в benefit.html

  if (btn) {
    btn.forEach((item) => {
      item.addEventListener('click', (evt) => {
        const input = evt.target;

        const modalId = input.dataset.id; // получает id окна по атрибуту data-id, которое нужно открыть

        const modalHash = input.dataset.hash; // получает hash окна по атрибуту data-hash, которое нужно открыть
        window.location.hash = modalHash;

        changeContent(modalId, '-picture', '.modal-picture', 'active-picture'); // меняет изображения
        changeContent(modalId, '-list', '.modal-list', 'active-list'); // меняет список с карточками
        changeContent(modalId, '-title', '.modal-title', 'active-title'); // меняет заголовок
      });
    });
  }

  setBenefit(); // устанавливает в benefit.html изначальный контент(для первого направления modal-professional)
  getLocation(); // переходит от main.html к нужному направлению в benefit.html
};

export {changeBenefit};
